@media screen and (max-width: 1600px) {
.hero-container {
    height: 200vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
    position: relative;
    top:-65px
    
}
.bg-video{
    object-fit: cover;
    width: 100%;
    height: 120vh;
    position: absolute;
    z-index: 0;
}

.hero-container > h5 {
    color: white;
    font-size: 70px;
    margin-top: -800px;
    padding:0 180px 0 180px;
    text-align: center;
    transition: 1s all ease-in-out;
    cursor: pointer;
    z-index: 0;
    font-weight: 400;
    position: fixed;
  }


  
  .hero-container > p {
    margin-top: -50px;
    color: white;
    font-size: 17px;
    padding: 0 350px 0 350px;
    text-align: center;
      transition: 1s all ease-in-out;
      cursor: pointer;
      z-index: 0;
      position: fixed;
  }

 

  .btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
    padding: 12px 26px;
    font-size: 20px;
  }

  
  .btn--outline:hover {
    transition: all 0.3s ease-out;
    background: #fff;
    color: #242424;
    transition: 250ms;
  }

  .btn--outline2 {
    transition: all 0.3s ease-out;
    background: #fff;
    color: #242424;
    transition: 250ms;
    padding: 12px 26px;
    font-size: 20px;
    border: 1px solid #fff;
  }
  
  .hero-btns {
    margin-top: 12px;
    z-index: 1;
  }

}
 

