.navbar {
    background: black;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
  
    z-index: 9;
    padding:0px 0px 0px 0px;
  }

  .dropdown .dropbtn {
    font-size: 15px;  
    border: none;
    outline: none;
    color: white;
    padding: 14px 16px;
    background-color: inherit;
    font-family: inherit;
    margin-top: 24px;
  }
  
  .dropbtn:hover{
    background-color: #139FF4 !important;
  }
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  
  
  
  .dropdown:hover .dropdown-content {
    display: block;
    
  }
  
  .navbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 30px;
    width:100%;
    padding:0px 0px 0px 0px;
  }
  .logo{
    width: 140px;
    color: white;
    transition: 0.2s all ease-in-out;
  }
  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 4rem;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
  }
  .navbar-logo:hover {
    color: #fff;
    justify-self: start;
    margin-left: 4rem;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
  }
  
  .fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .nav-menu {
    display: flex;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: flex-end;
    margin-right: 4rem;
    padding-top:0px;
    
  }
  
  .nav-item {
    height: 80px;
  }
  
  .nav-links {
    display: flex;
    align-items: center;
    text-decoration: none;

  

    font-size: 15px;  
    border: none;
    outline: none;
    color: white;
    padding: 14px 16px;
    background-color: inherit;
    font-family: inherit;
    margin-top: 24px;
  }

  .nav-links2 {
    float: none;
    color: white;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
    font-size: 15px;
    background-color:black;
    
  }
  
  .nav-links:hover {
    background-color: #139FF4 !important;
    color: white;
  }

  .nav-links2:hover {
    font-size: 15px;
    text-decoration: none;
    color: white;
    background-color: #139FF4 !important;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  .fa-bars {
    color: white;
   
  }
  .fa-times {
    color: white !important;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
    .logo{
      width:140px;
    }
  
    .navbar-container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 30px;
      width:100%;
      padding:0px 0px 0px 0px;
    }

    .navbar-logo {
        color: #fff;
        justify-self: start;
        cursor: pointer;
        text-decoration: none;
        font-size: 2rem;
        display: flex;
      margin-left: 10px;
        margin-top:-3px;
      }
  
    .nav-menu {
        padding:0px 0px 290px 0px;
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background:  black;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      display: flex;
      align-items: center;
      text-decoration: none;
  
    
      justify-content: center;
      font-size: 15px;  
      border: none;
      outline: none;
      color: white;
      padding: 14px 16px;
      background-color: inherit;
      font-family: inherit;
      margin-top: 24px;
    }
  
    .nav-links:hover {
      background-color: #139FF4 !important;
      color: white;
    }

  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
      margin-top:-7px;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #fff;
      padding: 14px 20px;
      border: 1px solid #fff;
      transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #242424;
      transition: 250ms;
    }

    .dropdown-content {
      display: none;
      position: absolute;
      background-color: #f9f9f9;
      min-width: 160px;
      margin-left: 160px;
      box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
      z-index: 1;
    }
    
  }

  @media screen and (min-width: 1600px) {
    .navbar {
      background: black;
      height: 100px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1.2rem;
      position:sticky;
      top: 0;

      z-index: 9;
      padding:0px 0px 0px 0px;
    }
    .nav-links {
      display: flex;
      align-items: center;
      text-decoration: none;
  
    
  
      font-size: 17px;  
      border: none;
      outline: none;
      color: white;
      padding: 14px 16px;
      background-color: inherit;
      font-family: inherit;
      margin-top: 24px;
    }
  
    .nav-links2 {
      float: none;
      color: white;
      padding: 12px 16px;
      text-decoration: none;
      display: block;
      text-align: left;
      font-size: 17px;
      background-color:black;
    }

    .dropdown .dropbtn {
      font-size: 17px;  
      border: none;
      outline: none;
      color: white;
      padding: 14px 16px;
      background-color: inherit;
      font-family: inherit;
      margin-top: 24px;
    }
    .logo{
      padding-top: 15px;
      width: 160px;
      color: white;
      transition: 0.2s all ease-in-out;
    }
  }