@media screen and (max-width: 1600px) {
.about-inner{
    background-color: black;
    padding: 50px 0 50px 0;
    margin-bottom: 130px;
}

.about-inner > h1{
    color:white;
}

.about-container{
    background-color: #101218;
    padding:0 0 150px 0;
    position: relative;
    z-index: 2;
}

.about-inner2{
    display:flex;
    padding:0 120px 0 180px;
}


.part-1 > small{
    color:#ff691f;
}

.part-1 > h3{
    color:white;
    font-size:36px;
    padding-top:10px;
    padding-bottom: 30px;
}

.part-1 > p{
    color:white;
    font-size: 14px;
}

.part-2{
    padding-left: 30px;
    padding-right: 30px;
}

.part-2-video{
    width: 500px;
    padding-top: 90px;
}

.part-3-image{
    width:220px;
    height: 150px;
    position: absolute;
    margin-left: 265px;
    margin-top: 110px;
}

.btn--outline2 {
    background-color: transparent;
    color: white;
    padding: 8px 20px;
    border: 1px solid black;
    transition: all 0.3s ease-out;
    padding: 20px 35px 20px 35px;
    font-size: 16px;
    border: 1px solid white;
    border-radius: 30px;
  }

  
  .btn--outline2:hover {
    transition: all 0.3s ease-out;
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
  
  .hero-btns2 {
    margin-top: 280px;
    margin-left: 265px;
    z-index: 1;
    width: 200px;
  }
}



