@media screen and (max-width: 1600px) {
.contact-container{
    background-color: #101218;;
    padding:0px 0px 150px 0px;
    position: relative;
    z-index: 2;
}

.contact-inner{
    margin-bottom:130px;
    padding:50px 50px;
    background-color: black;
    color: white;
}

.contact-inner-2
{
    display: flex;
    flex-direction: row;
    padding:0 120px 0 120px;
}
.contact-inner-3
{
    color:whitesmoke;
}
.contact-inner-3 > h3
{
    padding:50px 0 40px 0;
    font-size: 21px;
}

.cu-img {
    width:600px;
}

.btn--outline4 {
    background-color: transparent;
    color: white;
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    padding: 20px 35px 20px 35px;
    font-size: 16px;
    border: 2px solid white;
    border-radius: 30px;
  }
.btn--outline4:hover {
    background-color: white;
    color: black;
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    padding: 20px 35px 20px 35px;
    font-size: 16px;
    border: 2px solid white;
    border-radius: 30px;
  }
}

