@media screen and (max-width: 1600px) {
.dircon{
    padding:100px 70px 160px 100px;
    z-index: 2;
    align-items: center;
    position: relative;
    display: flex;
    flex-direction: row;
}

.dirinner > p{
    font-size: 16px;
    padding-top: 25px;
    padding-bottom: 25px;
    color: grey;
}
.dirinner > h3{
    font-size: 40px;

}

.btn--outline {
    background-color: transparent;
    color: black;
    padding: 8px 20px;
    border: 1px solid black;
    transition: all 0.3s ease-out;
    padding: 20px 35px 20px 35px;
    font-size: 16px;
    border: 1px solid black;
    border-radius: 30px;
  }

  
  .btn--outline:hover {
    transition: all 0.3s ease-out;
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
  
  .hero-btns {
    margin-top: 12px;
    z-index: 1;
  }

  .manager-img{
    width:330px;
    margin-left: 70px;
  }
}


