.CC-inner{
    padding:100px 0px 120px 0px;
}

.CC-inner > h2{
    color:#139FF4;
    padding-bottom: 30px;
    font-weight: 600;
    padding-top: 30px;
}

.CC-inner > h3{
    font-size: 21px;
}

.CC-inner2 {
    background-color: #e7e8ef;
    padding:100px 0px 140px 0px;
   
    column-gap: 50px;
}
.CC-part2 > h4 {
    font-size: 21px;
   
}
.CC-part2 > h2 {
    color:#139FF4;
    padding-bottom: 30px;
    font-weight: 600;
    padding-top: 30px;
}
.chat-img{
    margin-top: 30px;
    justify-content: center;
}

@media screen and (max-width: 960px) {
    .chat-img{
        margin-top: 80px;
    
        margin-bottom: -10px;
    }
}

@media screen and (min-width: 1600px) {

    .chat-img{
        margin-top: 100px;
    
        margin-bottom: -10px;
    }
}