@media screen and (max-width: 1600px) {
.services-container{
    position: relative;
    z-index:2;
    padding:0 0 150px 0;
    background-color: whitesmoke;
}

.services-inner{
    background-color: black;
    padding: 50px 0 50px 0;
    color:whitesmoke;
    margin-bottom: 130px;
}

.value-1 > small{
    color:#ff691f;
}

.value-1 > h3{
    color:black;
    font-size:36px;
    padding-top:10px;
    padding-bottom: 10px;
}

.services-inner-2{
