    padding: 8px 20px;
    border: 1px solid black;
    transition: all 0.3s ease-out;
    padding: 20px 35px 20px 35px;
    font-size: 16px;
    border-radius: 30px;
  }

  
  .btn--outline2:hover {
    transition: all 0.3s ease-out;
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
  
  .hero-btns3 {
    margin-top: 40px;
    margin-left: 0px;
    z-index: 1;
    width: 200px;
  }

  .services-container2{
    position: relative;
    z-index:2;
    padding:110px 0 150px 0;
    background-color: #e7e8ef;
}
.services-inner-3{
    padding:0 120px 0 140px;
    display: flex;
    column-gap: 0px;
}
.value-2{
    padding-top: 40px;
}

.value-2 > small{
    color:#ff691f;
}

.value-2 > h3{
    color:black;
    font-size:36px;
    padding-top:10px;
