.aboutus-container{
    background-color: #e7e8ef;
}
.aboutus-container > h1{
    padding:240px 0 290px 0;
    color: #139FF4;
    font-size: 100px;
    font-weight: 800;
}
.aboutus-inner{
    padding:40px 80px 15px 80px;
    z-index: 2;
    position: relative;
    background-color: white;
    display: flex;
}

.okok >h3{
    font-size: 23px;
}
.okok >h1{
    font-size: 30px;
    color:#139FF4;
    font-weight: 600;
    padding-bottom: 30px;
}
.okok{
    padding-top: 230px;
}

.aboutus-inner2{
    position: relative;
    background-color: #e7e8ef !important;
    padding:160px 140px 0px 140px;
}
.aboutus-inner2 >h1{
    color:#139FF4;
    font-size: 30px;
    padding-bottom: 30px;
    font-weight: 600;
}
.aboutus-inner2 >p {
    padding-bottom: 30px;
}

.au-img{
    right:0;
    position: absolute;
    z-index: 2;
    top:1200px;
}
.au-img2{
    width: 450px;
    padding-top: 0px;
    
}
.services-container2 {
    background-color: #e7e8ef !important;
}

.card-group {
    display:flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    column-gap: 20px;
    padding-top: 0px;
    margin-top:-50px;
}

.card1:hover {
   transform: scale(1.1);
   transition: 1s all ease-in-out;
   cursor: pointer;
}

.card2:hover {
    transform: scale(1.1);
    transition: 1s all ease-in-out;
    cursor: pointer;
 }

 .card3:hover {
    transform: scale(1.1);
    transition: 1s all ease-in-out;
    cursor: pointer;
 }

.card-group2 {
    display:flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    column-gap: 20px;
    padding-bottom: 60px;
}

.services-header {
    font-size: 40px;
    margin: 0px auto;
    padding-top: 50px;
    padding-bottom: 30px;
    color: #093F6E;
}

.card2 {
    max-width:300px;
    border:1px solid whitesmoke;
    padding:30px 30px 10px 30px;
    border-radius: 10px;
    background: #eeeeee;
    transition: 1s all ease-in-out;
    
}
.card1 {
    max-width:300px;
    border:1px solid whitesmoke;
    padding:30px 55px 10px 40px;
    border-radius: 10px;
    background: #eeeeee;
    transition: 1s all ease-in-out;
}

.card3 {
    max-width:300px;
    border:1px solid whitesmoke;
    padding:30px 30px 10px 30px;
    border-radius: 10px;
    background: #eeeeee;
    transition: 1s all ease-in-out;
}
.card-title {
    font-size: 18px;
    text-align: center;
    color: #093F6E;
}
.card-img-top2 {
    width:80px;
    margin-left: 50px;
    padding-bottom: 20px;
    
}
.card-img-top3 {
    width:80px;
    margin-left: 40px;
    padding-bottom: 20px;
}
.card-img-top {
    width:80px;
    margin-left: 30px;
    padding-bottom: 20px;
}
@media screen and (max-width: 768px) {

    .aboutus-container > h1{
        padding:240px 10px 290px 10px;
        color: #139FF4;
        font-size: 45px;
        font-weight: 800;
    }
    .card-group {
        display:flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        row-gap: 20px;

    }
    .card-group2 {
        display:flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        row-gap: 20px;
        margin-top:-40px;

    }

    .services-header {
        font-size: 35px;
        margin: 0px auto;
        padding-top: 50px;
        padding-bottom: 30px;
    }
    .aboutus-inner{
        padding:40px 20px 15px 20px;
        display: flex;
        flex-direction: column;
        z-index: 2;
        position: relative;
        background-color: white;
        display: flex;
    }

    .okok{
        padding:30px 20px 150px 20px;
    }
    .aboutus-inner2{
        position: relative;
        background-color: #e7e8ef !important;
        padding:120px 20px 0px 20px;
    }
    .au-img{
       right:0;
        position: absolute;
        z-index: 2;
        top:1100px !important;
        width: 200px !important;
    }

    .au-img2{
        width: 350px;
        padding-top: 0px;
        display: none;
        
    }
}

@media screen and (max-width: 1500px) {
    .au-img{
        right:0;
        position: absolute;
        z-index: 2;
        top:1300px;
        width: 400px;
    }
}

@media screen and (min-width: 1600px) {
    .aboutus-container{
        height: 92vh;
    }
    .aboutus-container > h1{
        padding:280px 0 290px 0;
        color: #139FF4;
        font-size: 140px;
        font-weight: 800;
    }
    .au-img{
        right:0;
        position: absolute;
        z-index: 2;
        top:1400px;
        width: 400px;
    }

}